.outContainer {
    width: 100%;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.4);
    z-index: 10;
    position: fixed;
    top: 0;
    left: 0;
}

.modal {
    width: 20rem;
    height: 20rem;
    background-color: #fff;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    z-index: 100;
    border: 1px solid white;

}

.modal_title {
    margin: 1rem;
    text-align: center;
}

.modal_desc {
    height: 8rem;
    margin-top: 2rem;
    text-align: center;
}

.modal_author {
    margin-right: 1rem;
    text-align: right;
}

.modal_close {
    background-image: url(https://www.citypng.com/public/uploads/preview/png-red-round-close-x-icon-31631915146jpppmdzihs.png);
    background-size: 100%;
    background-repeat: no-repeat;
    position: relative;
    left: 88%;
    top: -82%;
    width: 2rem;
    height: 2rem;
}